.dropdown-menu {
    background-color: var(--color-dark);
}

.dropdown-item {
    color: var(--color-light);
}

.cyporg-nav {
    min-height: 100px;
}

h1 {
    color: var(--color-light);
    margin: 0 0 0 45px;
    border: 1px solid var(--color-primary);
    padding: 0 7px;
    border-radius: var(--border-radius-primary);
    font-weight: 400;
    font-size: 20px;
}

h1 em {
    color: var(--color-primary);
    font-style: normal;
}